@-webkit-keyframes fade-in {
  0% {
      opacity: 0;
  }
  50% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

@keyframes fade-in {
  0% {
      opacity: 0;
  }
  50% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

.fade-in {
  -webkit-animation: fade-in 2s;
  -moz-animation: fade-in 2s;
  -o-animation: fade-in 2s;
  -ms-animation: fade-in 2s;
}



